<template>
    <div>
      <div class="input" ref="inputRef" contenteditable="true" @input="updateInputText"></div>
      <div class="output">{{ outputText }}</div>
      <button @click="submitData">Submit</button>
    </div>
  </template>
  
  <script>
  import { defineComponent, ref } from "vue";
  import axios from "axios";
  
  export default defineComponent({
    setup() {
      const inputRef = ref(null);
      const inputText = ref("");
      const outputText = ref("");
      const additionalData = ref("");
  
      const updateInputText = () => {
        if (inputRef.value) {
          inputText.value = inputRef.value.innerText;
        }
      };
  
      const submitData = async () => {
        const requestData = {
          url: "http://localhost:3000/api/data", // Replace with your API endpoint
          text: inputText.value,
        };
  
        try {
          const response = await axios.post(requestData.url, requestData);
          outputText.value = response.data.message;
          additionalData.value = response.data.additionalData;
        } catch (error) {
          console.error(error);
        }
  
        inputText.value = "";
      };
  
      return {
        inputRef,
        outputText,
        updateInputText,
        submitData,
      };
    },
  });
  </script>
  
  <style>
  .input {
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 20px;
    overflow-y: auto;
  }
  
  .output {
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 20px;
    overflow-y: auto;
  }
  
  .additional-data {
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 20px;
    overflow-y: auto;
  }
  </style>
  